.ai-interface-manager {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  background: #1e1e1e;
  color: #d4d4d4;
  font-family: 'Segoe UI', system-ui, sans-serif;
  overflow: hidden;
}

/* Loading Screen */
.ai-interface-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: #1e1e1e;
  color: #d4d4d4;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid #333;
  border-top: 3px solid #007acc;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-text h2 {
  margin: 0 0 10px 0;
  color: #007acc;
}

.loading-text p {
  margin: 0;
  color: #888;
}

/* Interface Layout */
.ai-interface-layout {
  display: flex;
  flex-direction: column;
  height: 100%;
}

/* Top Navigation */
.ai-top-nav {
  display: flex;
  align-items: center;
  height: 35px;
  background: #2d2d30;
  border-bottom: 1px solid #333;
  padding: 0 10px;
  z-index: 1000;
}

.ai-nav-left {
  display: flex;
  align-items: center;
  gap: 15px;
}

.ai-logo {
  display: flex;
  align-items: center;
  gap: 5px;
  font-weight: bold;
}

.logo-icon {
  font-size: 16px;
}

.logo-text {
  color: #007acc;
}

.page-breadcrumb {
  color: #888;
  font-size: 13px;
}

.ai-nav-center {
  flex: 1;
  display: flex;
  justify-content: center;
}

.global-search {
  position: relative;
}

.global-search-input {
  width: 300px;
  height: 24px;
  padding: 0 10px;
  background: #3c3c3c;
  border: 1px solid #555;
  border-radius: 4px;
  color: #d4d4d4;
  font-size: 13px;
  outline: none;
}

.global-search-input:focus {
  border-color: #007acc;
}

.ai-nav-right {
  display: flex;
  align-items: center;
  gap: 8px;
}

.mode-toggle,
.ai-toggle {
  width: 28px;
  height: 28px;
  background: transparent;
  border: 1px solid #555;
  border-radius: 4px;
  color: #d4d4d4;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
}

.mode-toggle:hover,
.ai-toggle:hover {
  background: #404040;
  border-color: #007acc;
}

/* Main Content */
.ai-main-content {
  display: flex;
  flex: 1;
  overflow: hidden;
}

/* Sidebar */
.ai-sidebar {
  width: 300px;
  background: #252526;
  border-right: 1px solid #333;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.sidebar-header {
  padding: 15px;
  border-bottom: 1px solid #333;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sidebar-header h3 {
  margin: 0;
  font-size: 14px;
  font-weight: 600;
}

.page-count {
  background: #007acc;
  color: white;
  padding: 2px 8px;
  border-radius: 10px;
  font-size: 11px;
}

.page-categories {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
}

.category-section {
  margin-bottom: 20px;
}

.category-title {
  font-size: 11px;
  font-weight: 600;
  color: #888;
  margin: 0 0 8px 0;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.category-pages {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.page-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px 8px;
  background: transparent;
  border: none;
  color: #d4d4d4;
  cursor: pointer;
  border-radius: 4px;
  font-size: 13px;
  text-align: left;
  width: 100%;
}

.page-button:hover {
  background: #2a2d2e;
}

.page-button.active {
  background: #094771;
  color: #ffffff;
}

.page-icon {
  font-size: 14px;
  width: 16px;
  text-align: center;
}

.page-title {
  flex: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Page Content */
.ai-page-content {
  flex: 1;
  background: #1e1e1e;
  overflow: auto;
  padding: 20px;
}

/* AI Chat Panel */
.ai-chat-panel {
  width: 400px;
  background: #252526;
  border-left: 1px solid #333;
  display: flex;
  flex-direction: column;
}

.embedded-grok {
  height: 100%;
  border: none;
}

/* Bottom Panel */
.ai-bottom-panel {
  background: #252526;
  border-top: 1px solid #333;
  min-height: 200px;
  max-height: 400px;
  resize: vertical;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.bottom-section {
  flex: 1;
  overflow: auto;
}

.status-bar {
  height: 22px;
  background: #007acc;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
  font-size: 12px;
}

.status-left {
  display: flex;
  gap: 15px;
}

.status-item {
  display: flex;
  align-items: center;
}

.status-right {
  display: flex;
  gap: 8px;
}

.panel-toggle {
  background: transparent;
  border: none;
  color: white;
  cursor: pointer;
  padding: 2px 8px;
  border-radius: 3px;
  font-size: 11px;
}

.panel-toggle:hover {
  background: rgba(255, 255, 255, 0.1);
}

.panel-toggle.active {
  background: rgba(255, 255, 255, 0.2);
}

/* Active Components Overlay */
.active-components-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 10000;
}

.ai-component {
  position: absolute;
  pointer-events: auto;
}

.ai-component-widget {
  background: #252526;
  border: 1px solid #333;
  border-radius: 4px;
  padding: 10px;
}

.ai-component-panel {
  background: #1e1e1e;
  border: 1px solid #333;
  border-radius: 4px;
}

.ai-component-modal {
  background: #2d2d30;
  border: 1px solid #333;
  border-radius: 6px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
}

.ai-component-overlay {
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(2px);
}

/* Mode Variations */
.mode-minimal .ai-sidebar {
  width: 60px;
}

.mode-minimal .page-title {
  display: none;
}

.mode-minimal .sidebar-header h3,
.mode-minimal .page-count {
  display: none;
}

.mode-focus .ai-sidebar,
.mode-focus .ai-chat-panel {
  display: none;
}

.mode-focus .ai-page-content {
  padding: 40px;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .ai-sidebar {
    width: 250px;
  }
  
  .ai-chat-panel {
    width: 320px;
  }
}

@media (max-width: 768px) {
  .ai-main-content {
    flex-direction: column;
  }
  
  .ai-sidebar,
  .ai-chat-panel {
    width: 100%;
    height: 200px;
  }
  
  .global-search-input {
    width: 200px;
  }
}

/* Animations */
.ai-interface-manager * {
  transition: all 0.2s ease;
}

.page-button {
  transition: background-color 0.15s ease;
}

.ai-component {
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
